body {
  margin: 0;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo a {
  width: 40px;
  height: 40px;
  display: block;
}

.logo img {
  width: 100%;
  height: 100%;
}

.navigation ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.navigation li {
  margin-left: 20px;
}

.navigation li:first-child {
  margin-left: 0;
}

.navigation a {
  white-space: nowrap;
  text-transform: uppercase;
  color: #000;
  align-items: center;
  height: 60px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
  display: flex;
  position: relative;
}

.navigation a:hover, .navigation a.is-active {
  color: #00acdc;
}

.navigation a.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
